.play_btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.play_btn img {
  margin-left: 4px;
}
.singer_details_back {
  background: #16235b;
  padding: 3px 0 0 0;
  z-index: 5;
  width: 100%;
}
.singer_details_back img {
  width: 42px;
}
.singer_details_info {
  background: #0d163f;
  padding: 0 0 0px 0;
  width: 100%;
  display: flex;
}
.singer_details_left {
  width: 40%;
  height: 150px;
  margin: 13px;
}
.singer_details_left img {
  height: 100%;
}
.singer_details_right {
  color: #f7f1db;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin: 13px 0;
  width: 60%;
}
.singer_details_name {
  font-size: 22px;
}
.singer_all_song_play button {
  margin: 0;
  padding: 0;
  border: 1px solid transparent;
  outline: none;
  width: 100%;
  height: 41px;
  background: #16235b;
  color: #f7f1db;
  border-radius: 5px;
}
.singer_all_song_play button span {
  font-size: 16px;
}
.singer_details_song {
  background: #16235b;
}
.singer_details_song_title {
  line-height: 40px;
  display: flex;
  color: #f7f1db;
}
.singer_details_song_title li {
  font-weight: bold;
}
.singer_details_song_item {
  height: 50px;
  display: flex;
  align-items: center;
  border-bottom: 2px solid #0d163f;
}
.singer_details_song_item li {
  text-align: left;
  color: #f7f1db;
}
.singer_details_song_item>li:nth-child(1),
.singer_details_song_title>li:nth-child(1) {
  width: 30%;
  text-indent: 1em;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.singer_details_song_item>li:nth-child(2),
.singer_details_song_title>li:nth-child(2) {
  width: 30%;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.singer_details_song_item>li:nth-child(3),
.singer_details_song_title>li:nth-child(3) {
  text-align: center;
  width: 20%;
}
.singer_details_song_item>li:nth-child(4),
.singer_details_song_title>li:nth-child(4) {
  text-align: center;
  font-size: 10px;
  width: 20%;
}
.singer_details_song_item:nth-child(even) {
  background: #0d163f;
}
.singer_details_song_item:nth-child(odd) {
  background: #16235b;
}
.singerDetailsScroll_container {
  position: absolute;
}
.singer_details_add {
  margin-left: 4px;
  width: 24px;
}
.singer_details_love {
  width: 24px;
  margin-left: 10px;
}
